import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import CardLayout from "../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Label } from "reactstrap";
import Loader from "../../components/Loader/Loader";
import _ from "lodash";
import { Async } from "react-select";
import "react-select/dist/react-select.css";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import AppConfig from "../../constants/AppConfig";
import VideoPlayer from "../../components/VideoPlayer/VideoPlayer";
import DropdownSelect from "../../components/InputElement/Dropdown";

class VideoAllocationForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      videoAllocation: {
        videoRequired: false,
        LanguageCodeRequired : false,
        LanguageCode : ""
      },
      cardTitle: "",
      videoCategory: "",
      videoValue: "",
      renderURL: "",
      error: ""
    };
  }
  componentDidMount() {
    let videoCategory = this.props.match.params.videoCategory;
    //for video allocation : Filepath is updated in crop/cropStep/cropMaterial data
    //Hence getting the data in case of refresh page
    if (videoCategory === "crop") {
      this.props.getCropsList();
    } else if (videoCategory === "cropStep") {
      this.props.getCropSteps();
    } else {
      this.props.getCropStepsMaterial();
    }
    setTimeout(() => {
      let id = this.props.match.params.id;
      let videoCategory = this.props.match.params.videoCategory;
      let videoAllocation = {};
      if (videoCategory === "crop") {
        videoAllocation = _.find(this.props.activeCrops, function(crop) {
          return crop.Id === parseInt(id);
        });
      } else if (videoCategory === "cropStep") {
        videoAllocation = _.find(this.props.activeCropSteps, function(
          cropStep
        ) {
          return cropStep.Id === parseInt(id);
        });
      } else {
        videoAllocation = _.find(this.props.activeCropMaterial, function(
          cropMaterial
        ) {
          return cropMaterial.Id === parseInt(id);
        });
      }
      if (videoAllocation !== undefined) {
        videoAllocation.videoRequired = false;
        this.setState({
          loading: false,
          videoAllocation
        });
      } else {
        this.goBack();
      }
    }, 2000);
  }
  goBack() {
    this.props.history.goBack();
  }
  handleVideoChange = videoSelected => {
    let videoAllocation = { ...this.state.videoAllocation };
    if (videoSelected !== null) {
      videoAllocation.VideoPath = videoSelected.FilePath;
      videoAllocation.videoRequired = false;
      let videoValue = videoSelected;
      let renderURL = `${AppConfig.serverURL}/${videoSelected.FilePath}`;
      this.setState({ videoAllocation, renderURL, videoValue });
    } else {
      videoAllocation.VideoPath = "";
      this.setState({ videoAllocation, renderURL: "", videoValue: "" });
    }
  };
  handleLanguageChange(languageValue) {
    let videoAllocation = { ...this.state.videoAllocation };
    videoAllocation.LanguageCode = languageValue;
    videoAllocation.LanguageCodeRequired = false;
    this.setState({ videoAllocation });
  }
  onSubmit() {
    let videoAllocation = { ...this.state.videoAllocation };
    if (videoAllocation.VideoPath && videoAllocation.LanguageCode) {
      videoAllocation.UpdatedBy = localStorage.getItem("user");
      videoAllocation.UpdatedOn = new Date();
      let videoCategory = this.props.match.params.videoCategory;
      let video = _.pick(videoAllocation, [
          "Id",
          "VideoPath",
          "LanguageCode",
          "UpdatedBy",
          "UpdatedOn"
        ]);
        this.props.updateCropStepVideo(video.Id, video);
      let message = "";
      let compRef = this;
      this.setState({ loading: true });    
      setTimeout(() => {
        compRef.props.cropStepError
          ? (message = "Something went wrong !")
          : (message = "Video allocation created successfully");
        compRef.onReset();
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.cropStepError);
        setTimeout(() => {
          if (!compRef.props.cropStepError) {
            compRef.goBack();
          }
        }, 1000);
      }, 1000);
    } else {
      if(!videoAllocation.VideoPath){
        videoAllocation.videoRequired = true;
      }
      if(!videoAllocation.LanguageCode){
        videoAllocation.LanguageCodeRequired = true;
      }   
      this.setState({ videoAllocation });
    }
  }
  onReset() {
    let videoAllocation = { ...this.state.videoAllocation };
    videoAllocation.videoRequired = false;
    videoAllocation.FilePath = "";
    videoAllocation.LanguageCode = "";
    videoAllocation.VideoPath = "";
    videoAllocation.LanguageCodeRequired = false,
    this.setState({
      videoAllocation,
      videoValue: "",
      renderURL: "",
      error: ""
    });
  }
  render() {
    const options = this.props.videoOptions;
    const getOptions = (input, callback) => {
      let filterOpt = [];
      if (input) {
        filterOpt = _.filter(options, function(video) {
          if (
            _.includes(video.label.toLowerCase(), input.toLowerCase()) ||
            video.label.toLowerCase() == input.toLowerCase()
          ) {
            return video;
          }
        });
      }
      setTimeout(() => {
        callback(null, {
          options: filterOpt,
          complete: true
        });
      }, 500);
    };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Video Allocation Form"
        navigation={true}
        navigationRoute={this}
        onClick={this.goBack.bind(this)}
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="6">
              <FormGroup row>
                <Col xs="12" md="10">
                  <Label>Video</Label>
                  <Async
                    name="form-field-name"
                    cacheOptions
                    value={this.state.videoValue}
                    defaultOptions
                    onChange={this.handleVideoChange.bind(this)}
                    loadOptions={getOptions}
                  />
                  {this.state.videoAllocation.videoRequired ? (
                    <div className="help-block">*Video is required</div>
                  ) : null}
                </Col>
              </FormGroup>
              <FormGroup row >
              <Col xs="12" md="10">
              <Label>Language</Label>
              <DropdownSelect
                name="Language"
                placeholder="Select language "
                options={this.props.languagesList}
                value={this.state.videoAllocation.LanguageCode}
                required={this.state.videoAllocation.LanguageCodeRequired}
                onChange={this.handleLanguageChange.bind(this)}
                clear ={true}
              />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="2">
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Create
                  </Button>
                </Col>
                <Col md="2">
                  <Button
                    className="theme-reset-btn"
                    onClick={this.onReset.bind(this)}
                  >
                    Reset
                  </Button>
                </Col>
              </FormGroup>
            </Col>

            {this.state.renderURL ? (
              <Col md="6">
                <Col xs="12" md="10">
                  <VideoPlayer
                    autoPlay={false}
                    source={this.state.renderURL}
                    mute={true}
                  />
                  </Col>
              </Col>
            ) : null}
          </FormGroup>
        </div>
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}
export const mapStateToProps = state => {
  return {
    videoOptions: state.mediaReducer.videoOptions,
    videoFiles: state.mediaReducer.videoFiles,
    cropStepError: state.cropsReducer.cropStepError,
    activeCropSteps: state.cropsReducer.activeCropSteps,
    languagesList: state.languagesReducer.languagesList,
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    getCropSteps: () => dispatch(actions.getCropSteps()),
      updateCropStepVideo: (id, videoAllocate) =>
      dispatch(actions.updateCropStepVideo(id, videoAllocate))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  VideoAllocationForm
);



